        .on-case-study{
                position: absolute;
                z-index: 10;
                min-height: 56px;
                margin-top: 56px;

                text-align: right;
                width: 100%;
            

        }


        .projectThumb{
            width: 100%;
            height: 320px;
            background-color: #101010;
            border-radius: 8px;
            margin-bottom: 16px;
            text-align: center;
            float: left;
            overflow: hidden;
            background-position: center;
            background-size: cover;
             a{
                text-decoration: none;
                color: black;
                background-color: #f0f0f0;
                border-radius: 8px;
                height: 100%;
                width: 100%;
            }
        }

     .projectThumb .thumbLabel{
                    display: flex;
                    align-items:center;
                    justify-content:center;
                    background-color: #f0f0f0;
                    opacity: 0;
                    height: 100%;
                    transform: scale(1);
                    transition: all 300ms;

                }

        .projectThumb:hover .thumbLabel{
                    display: flex;
                    align-items:center;
                    justify-content:center;
                    background-color: #f0f0f0;
                    opacity: 0.9;
                    height: 100%;
                    transform: scale(1.05);
                    transition: all 600ms;

                }

        


     @media only screen and (min-width: 768px) {


    h1.workTitle{
        font-size: 2em;
    }


    .site-title h3{
        font-size: 1.17em;
    }

    .site-nav {
        width: calc(100% - 300px);
    }

    .site-nav.on-case-study {
        width: 100%;
    }

    .flex-container {
      display: flex;
    }

     .cs-overview-col{
        display: flex;
        flex-direction: column;
        flex-basis: 100%;
        flex: 1;
        margin-right: 30px;
            &:last-of-type {
                margin-right: 0;
            }

     }


      .cs-white-center, .cs-grey-center{
            .paragraph p.description{
                max-width: 40%;
                margin: auto;
            }
        }

        .projectThumb{
            width: 49%;
            float: left;
            margin-right:2%;

            &:hover {
                transition: all .3s ease;
                -moz-transition: all .3s ease;
                -webkit-transition: all .3s ease;
                -o-transition: all .3s ease;

            }

        }


        .projectThumb:nth-child(2n) {
        margin-right:0%;
        }

        .thumbLabel{
            display: none;

            &:hover {
                display: block;

            }
        }
         
    }

        